body {
  font-family: Tahoma, Arial, Helvetica, sans-serif;
  background-color: #192b39;
  text-align: center;
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px 15%;
}
.logo {
  width: 238px;
  height: 238px;
}
h1 {
  color: #ebb04d;
  font-weight: lighter;
}
hr {
  border-style: solid;
  border-width: 1px; /* adjust the thickness as needed */
  color: #ebb04d;
}
h2 {
  color: #ebb04d;
  font-weight: lighter;
  text-align: left;
  text-transform: uppercase;
}
